.skills-page {
    box-sizing: border-box;
    padding-top: 1rem;
    padding-left: 5rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 3rem;
    overflow-x: hidden;
}

.skills-description {
    flex: 1;
}

.skills-description>div {
    width: 100%;
}

.skills-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.skills-content-container>.paragraph-container {
    width: 100%;
}

.skills-progress-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
    flex: 1;
    margin-top: 2rem;
}

.resume {
    display: inline-block;
    padding: 0 3rem;
    width: 30rem;
    transform-style: preserve-3d;
}