@media only screen and (max-width: 1200px) {
    .brief {
        width: 80%;
    }

    .about-page .paragraph-container {
        width: 80%;
    }

    .skills-page {
        flex-direction: column;
        align-items: flex-start;
    }

    .skills-description .paragraph-container {
        width: 80%;
    }

    .skills-content-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    .skills-progress-container {
        justify-content: space-evenly;
        width: 80%;
        margin-top: 3rem;
    }

    .resume-container {
        width: 80%;
        text-align: center;
    }

    .resume-container>a>img {
        padding: 0;
        width: 100%;
    }

    .experience-slot {
        max-width: 80%;
    }
}

@media only screen and (max-width: 992px) {
    #logo {
        font-size: 4.5rem;
    }

    #name {
        font-size: 1.3rem;
    }

    #tag {
        font-size: 0.9rem;
    }

    .routes-list>li>a {
        font-size: 0.9rem;
    }

    .nav-social>a>svg {
        width: 1.5rem;
        height: 1.5rem;
    }

    .alphabet-wrapper>span {
        font-size: 4rem;
    }

    .brief {
        width: 90%;
        font-size: 1rem;
    }

    .future-button {
        width: 150px;
        height: 40px;
        font-size: 0.9em;
    }

    .future-button span {
        width: 7px;
        height: 7px;
    }

    .about-page .paragraph-container {
        width: 90%;
    }

    .about-page .paragraph-container .paragraph {
        font-size: 1.1rem;
    }

    .skills-page {
        flex-direction: column;
        align-items: flex-start;
    }

    .skills-description .paragraph-container {
        width: 90%;
    }

    .skills-page .paragraph-container .paragraph {
        font-size: 1.1rem;
    }

    .skills-progress-container {
        justify-content: space-evenly;
        width: 90%;
        margin-top: 3rem;
    }

    .resume-container {
        width: 90%;
        text-align: center;
    }

    .experience-slot {
        max-width: 90%;
    }

    .contact-page>div {
        width: 90%;
    }
}

@media only screen and (max-width: 768px) {

    .home-page,
    .about-page,
    .skills-page,
    .experience-page,
    .contact-page {
        padding-left: 3rem;
    }

    .paragraph-container {
        gap: 1rem;
    }

    #logo {
        font-size: 4.5rem;
    }

    #name {
        font-size: 1.3rem;
    }

    #tag {
        font-size: 0.9rem;
    }

    .brief {
        line-height: normal;
    }

    .routes-list>li>a {
        font-size: 0.9rem;
    }

    .nav-social>a>svg {
        width: 1.5rem;
        height: 1.5rem;
    }

    .alphabet-wrapper>span {
        font-size: 4rem;
    }

    .brief {
        width: 90%;
        font-size: 1rem;
    }

    .future-button {
        width: 150px;
        height: 40px;
        font-size: 0.9em;
    }

    .future-button span {
        width: 7px;
        height: 7px;
    }

    .about-page .paragraph-container {
        width: 90%;
    }

    .about-page .paragraph-container .paragraph {
        font-size: 1.1rem;
    }

    .skills-page {
        flex-direction: column;
        align-items: flex-start;
    }

    .skills-description .paragraph-container {
        width: 90%;
    }

    .skills-page .paragraph-container .paragraph {
        font-size: 1.1rem;
    }

    .skills-progress-container {
        justify-content: space-evenly;
        width: 90%;
        margin-top: 3rem;
    }

    .experience-slot {
        max-width: 90%;
    }

    .description {
        font-size: 1rem;
    }

    .tag {
        font-size: 1rem;
    }

    .contact-page>div {
        width: 90%;
    }

    .contact-page>div>.paragraph-container>p,
    .contact-page>div>.paragraph-container>a>p {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 576px) {
    html {
        --nav-aside-width: 53.6px;
    }

    .home-page,
    .about-page,
    .skills-page,
    .experience-page,
    .contact-page {
        padding-left: 1.5rem;
    }

    .nav-logo {
        padding: 0;
    }

    #nav-aside {
        gap: 1rem;
    }

    #logo {
        flex-shrink: 0;
        width: 45px;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #111;
        border-radius: 4px;
        margin: 0.5rem 0;
        font-size: 2.2rem;
    }

    .border-right {
        border-right: 2px solid var(--theme-color);
    }

    .routes-list>li {
        writing-mode: vertical-lr;
        border: none !important;
        width: auto;
    }

    .routes-list>li>a {
        color: #ebecf379 !important;
    }

    .nav-social {
        flex-direction: column;
        gap: 1rem;
    }

    .nav-social>a>svg {
        fill: #ebecf379 !important;
    }

    .alphabet-wrapper>span {
        font-size: 3.5rem;
    }

    .brief {
        width: 95%;
    }

    .about-page .paragraph-container .paragraph {
        color: #ebecf3;
    }

    .experience-slot {
        max-width: 95%;
    }
}

@media only screen and (max-width: 450px) {
    html {
        --nav-aside-width: 10vw;
    }

    #nav-aside {
        gap: 1.5rem;
    }

    #logo {
        width: 90%;
        height: inherit;
        font-size: 8vw;
    }

    .routes-list {
        gap: 1rem;
    }

    .routes-list>li>a {
        padding: 2vw;
    }

    .alphabet-wrapper>span {
        font-size: 2.7rem;
    }

    .home-page {
        gap: 1.5rem;
    }

    .brief {
        font-size: 0.75rem;
        font-weight: normal;
    }

    .future-button {
        margin-top: 1rem;
        width: 130px;
        height: 30px;
        font-size: 0.7em;
    }

    .future-button span {
        width: 5px;
        height: 5px;
    }

    .about-page .paragraph-container,
    .skills-page .paragraph-container,
    .experience-page .paragraph-container {
        width: 95%;
        text-align: justify;
    }

    .about-page>div>.alphabet-wrapper>span,
    .skills-page>div>.alphabet-wrapper>span,
    .experience-page>div>.alphabet-wrapper>span {
        font-size: 2.3rem;
    }

    .skills-description .paragraph-container {
        width: 95%;
    }

    .skills-page .paragraph-container .paragraph {
        font-size: 1.1rem;
    }

    .skills-progress-container {
        justify-content: space-evenly;
        width: 95%;
        margin-top: 3rem;
        justify-content: space-between;
    }

    .resume-container {
        width: 95%;
        text-align: center;
    }

    .experience-slot {
        max-width: 95%;
        font-size: 1rem;
    }

    .description {
        text-align: justify;
    }

    .tags-container>div {
        font-size: 0.7rem;
    }
}