#nav-aside {
    width: var(--nav-aside-width);
    background-color: var(--nav-aside-color);
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem;
}

.nav-logo {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;
    padding-bottom: 1rem;
    width: 100%;
    color: white;
    background-color: black;
}

.nav-logo-holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#logo {
    display: inline-block;
    color: var(--theme-color);
    font-size: 5.5rem;
    font-weight: bold;
}

#name {
    font-size: 1.7rem;
    letter-spacing: 0.1rem;
    color: white;
}

#tag {
    font-family: "secondary-font";
    font-size: 0.9rem;
    font-weight: lighter;
    text-align: center;
    color: #a5a5a5;
    letter-spacing: 0.05rem;
}

.nav-route {
    width: 100%;
}

.routes-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.routes-list>li {
    border-top: 2px solid rgba(255, 255, 255, 0.1);
    width: 100%;
}

.routes-list>li:last-child {
    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.routes-list>li:hover>a {
    color: var(--theme-color) !important;
}

.routes-list>li>a {
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    padding: 1rem;
    font-family: "secondary-font";
    font-weight: 400;
    font-size: 0.95rem;
    color: white;
    transition: all linear 150ms;
}

.nav-social {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.nav-social>a {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-social>a:hover>svg {
    transform: scale(1.3) !important;
    fill: var(--theme-color);
}

.nav-social>a>svg {
    display: inline-block;
    height: 1.7rem;
    width: 1.7rem;
    fill: white;
    transition: transform linear 150ms, fill linear 300ms;
}