.future-button {
    position: relative;
    width: 200px;
    height: 50px;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--theme-color);
    letter-spacing: 0.25em;
    font-size: 1.2em;
    outline: none;
    border: none;

    font-family: 'secondary-font';
    font-weight: normal;
}

.future-button span {
    position: absolute;
    width: 10px;
    height: 10px;
    transition: 0.5s;
}

.future-button span:nth-child(1) {
    top: -2px;
    left: -2px;
    border-bottom: 2px solid var(--theme-color);
    border-right: 2px solid var(--theme-color);
    transition-delay: 0s;
}

.future-button span:nth-child(2) {
    top: -2px;
    right: -2px;
    transform: rotate(90deg);
    border-bottom: 2px solid var(--theme-color);
    border-right: 2px solid var(--theme-color);
    transition-delay: 0.25s;
}

.future-button span:nth-child(3) {
    bottom: -2px;
    right: -2px;
    transform: rotate(180deg);
    border-bottom: 2px solid var(--theme-color);
    border-right: 2px solid var(--theme-color);
    transition-delay: 0.5s;
}

.future-button span:nth-child(4) {
    bottom: -2px;
    left: -2px;
    transform: rotate(270deg);
    border-bottom: 2px solid var(--theme-color);
    border-right: 2px solid var(--theme-color);
    transition-delay: 0.75s;
}

.future-button:hover span:nth-child(1) {
    top: calc(100% - 8px);
    left: calc(100% - 8px);
}

.future-button:hover span:nth-child(2) {
    top: calc(100% - 8px);
    right: calc(100% - 8px);
    transition-delay: 0.25s;
}

.future-button:hover span:nth-child(3) {
    bottom: calc(100% - 8px);
    right: calc(100% - 8px);
    transition-delay: 0.5s;
}

.future-button:hover span:nth-child(4) {
    bottom: calc(100% - 8px);
    left: calc(100% - 8px);
    transition-delay: 0.75s;
}