.contact-page {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-page>div {
    width: 60%;
}

.contact-page>div>div:nth-child(1) {
    text-align: center;
}

.contact-page>div>.paragraph-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-page>div>.paragraph-container {
    width: 100%;
    text-align: center;
}

.contact-page>div>.paragraph-container>p {
    font-weight: lighter;
    font-size: 1.5rem;
}

.contact-page>div>.paragraph-container>a {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    color: white;
    transition: all 150ms linear;
}

.contact-page>div>.paragraph-container>a:hover {
    color: var(--theme-color);
}

.contact-page>div>.paragraph-container>a>p {
    font-family: 'Secondary-Font';
    font-weight: normal;
    font-size: 1.5rem;
}