.home-page {
    box-sizing: border-box;
    padding-top: 1rem;
    padding-left: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 2rem;
}

.future-button-cover {
    display: inline-block;
}

.brief {
    font-family: 'secondary-font';
    color: #8D8D8D;
    font-size: 1.1rem;
    font-weight: 200;
    letter-spacing: 2px;
    text-transform: capitalize;
    width: 60%;
    line-height: 2rem;
}