.tag {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    background-color: #232323;
    border-radius: 1000px;
    padding: 0.3rem;
    text-align: center;
    width: fit-content;
    color: white;
    font-family: 'Secondary-Font';
    font-weight: normal;
    font-size: 1rem;
}