/* CSS Reset */
* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    font-family: "primary-font";
}

/* Customizing scroll bar */
::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-track {
    background: var(--theme-background);
}

::-webkit-scrollbar-thumb {
    background: var(--theme-color);
    border-radius: 1000px;
}

/* Changing the selection color */
::selection {
    color: var(--theme-background);
    background: var(--theme-color);
}

/* Defining Base Font */
@font-face {
    font-family: "primary-font";
    src: url("../fonts/EuropaGroteskSH-Med.otf");
}

@font-face {
    font-family: "secondary-font";
    src: url("../fonts/HVD Fonts - BrandonText-Bold.otf");
    font-weight: bold;
}

@font-face {
    font-family: "secondary-font";
    src: url("../fonts/HVD Fonts - BrandonText-Light.otf");
    font-weight: lighter;
}

@font-face {
    font-family: "secondary-font";
    src: url("../fonts/HVD Fonts - BrandonText-Regular.otf");
    font-weight: normal;
}

@font-face {
    font-family: "secondary-font";
    src: url("../fonts/HVD Fonts - BrandonText-Thin.otf");
    font-weight: 100;
}

/* Html variables */
html {
    --theme-color: #0aff9d;
    --theme-background: #1D1D1D;
    --nav-aside-width: 8.125rem;
    --nav-aside-color: #181818;
    --logo-stroke-width: 30px;
    --logo-stroke-color: white;
}

/* Background for the root element */
#root {
    background: var(--theme-background);
}

/* Configuration for the sections that navbar will route to */
.content-page-config {
    width: calc(100vw - var(--nav-aside-width));
    margin-left: auto;
    background-color: var(--theme-background);
    min-height: 100vh;
}

.logo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    min-height: 100vh;
}

.loader-text {
    color: var(--theme-color);
    text-transform: capitalize;
    font-family: 'secondary-font';
    font-size: 0.8rem;
}

.paragraph-container {
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 2rem;
    margin-top: 2rem;
}

.paragraph {
    font-size: 1.2rem;
    font-family: 'Secondary-Font';
    font-weight: normal;
    color: white;
}

.paragraph-link {
    font-family: 'Secondary-Font';
    font-weight: normal;
    color: var(--theme-color);
}