.progress-card {
    position: relative;
    cursor: pointer;
}

.progress-card>svg {
    transform: rotateZ(270deg) rotateY(180deg);
}

.progress-card>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.progress-card>div>span {
    font-size: 1.5rem;
    font-family: 'Primary-Font';
    color: var(--theme-color);
    text-transform: capitalize;
}

.progress-card>div>span:nth-child(2) {
    font-size: 1rem;
}