.experience-slot {
    max-width: 70%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    font-size: 1.1rem;
    padding-bottom: 1rem;
    border-bottom: 0.1px solid rgba(255, 255, 255, 0.3)
}

.experience-slot>div:nth-child(1),
.experience-slot>div:nth-child(2) {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.company,
.role {
    font-family: 'Primary-Font';
    font-weight: normal;
    color: white;
    letter-spacing: 0.1rem;
}

.company {
    font-size: 1.7rem;
}

.role {
    color: var(--theme-color);
}

.duration,
.location,
.description {
    font-family: 'Secondary-Font';
    font-weight: normal;
    color: white;
}

.description {
    opacity: 0.7;
    font-weight: lighter;
}

.tags-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 0.5rem;
}